.contacts {
  padding-bottom: 66px;
  @media screen and (min-width: 1200px) {
    padding-bottom: 96px;
  }

  .contact-box {
    @media screen and (min-width: 768px) {
      display: flex;
      column-gap: 20px;
      height: 642px;
    }
    @media screen and (min-width: 1200px) {
      height: 678px;
    }

    .contact-img {
      display: none;
      @media screen and (min-width: 768px) {
        display: block;
        width: 367px;
        height: 642px;
      }
      @media screen and (min-width: 1200px) {
        width: 565px;
          height: 678px;
      }
    }

    .contact-content {
      @media screen and (min-width: 768px) {
        width: 367px;
      }
      @media screen and (min-width: 1200px) {
        width: 747px;
      }

      .section-name {
        @extend %sectionName;
        margin-bottom: 24px;
        @media screen and (min-width: 768px) {
          font-size: 16px;
          margin-bottom: 32px;
        }
      }
      .section-title {
        width: 305px;
        margin-bottom: 16px;
        @extend %sectionTitle;
        @media screen and (min-width: 768px) {
          width: 244px;
           font-size: 38px ;
            //  margin-bottom: 24px;
        }
        @media screen and (min-width: 1200px) {
          width: 100%;
          font-size: 58px ;
            //  margin-bottom: 24px;
        }
      }
      .section-text {
        @extend %sectionText;
        margin-bottom: 16px;
        @media screen and (min-width: 768px) {
          display: none;
        }
        @media screen and (min-width: 1200px) {
          display: block;
          margin-bottom: 24px;
          width: 486px;
        }
      }

      .form {
        display: flex;
        flex-direction: column;

        .form-input {
          height: 53px;
          margin-bottom: 16px;
          padding: 16px;
          border: 1px solid $primaryTextColor;
          border-radius: 30px;
        }

        .form-area {
          height: 99px;
          margin-bottom: 16px;
          padding: 16px;
          border: 1px solid $primaryTextColor;
          border-radius: 30px;
          resize: none;

          .form-area::placeholder {
            font-size: 16px;
            line-height: 1.3;
            color: #818284;
          }
        }

        .policy-wrap {
          display: flex;
          align-items: start;
          margin-bottom: 16px;

          .checkbox-label {
            width: 622px;
            line-height: 1.5;

            .policy-link {
              color: inherit;
              text-decoration: underline;
            }
          }
        }

        .form-button {
          // border: 1px solid #000;
          border-radius: 30px;
          padding: 16px 24px;
          // width: 654px;
          height: 54px;
          background: $primaryTextColor;

          font-size: 16px;
          line-height: 1.4;
          color: #fff;

          &:hover {
            background-color: $primaryTextColor;
            outline: 1px solid #2c3847;
            box-shadow: 0 4px 16px #00000040;
          }
          &:active {
            background-color: #293441;
            outline: 1px solid $primaryTextColor;
          }

          &:disabled {
            background-color: #89898a;
            outline: 1px solid $primaryTextColor;
          }
        }
      }
    }
  }
}
